(function() {
  angular.module('wut', ['ngAnimate', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr']);

}).call(this);


(function() {
  angular.module('wut').controller('HomeCtrl', function($scope, $timeout, bootstrap) {
    var setCurrent;
    this.questionsLeft = [];
    $scope.bootstrap = bootstrap;
    setCurrent = (function(_this) {
      return function() {
        if (_this.questionsLeft.length === 0) {
          _this.questionsLeft = _.shuffle(bootstrap.questions);
        }
        $scope.currentQuestion = _this.questionsLeft.pop();
        return $scope.state = 'PROMPT';
      };
    })(this);
    $scope.submit = (function(_this) {
      return function(index) {
        _this.selected = index;
        $scope.state = index === $scope.currentQuestion.answer ? 'RIGHT' : 'WRONG';
        return _this.timeout = $timeout(setCurrent, bootstrap.timeout || 2000);
      };
    })(this);
    $scope.choiceClass = (function(_this) {
      return function(index) {
        if (_this.selected === index) {
          if ($scope.state === 'RIGHT') {
            return 'btn-success';
          } else if ($scope.state === 'WRONG') {
            return 'btn-danger';
          }
        }
      };
    })(this);
    $scope.backgroundStyle = function() {
      return {
        backgroundImage: 'url(' + bootstrap.background + ')'
      };
    };
    return setCurrent();
  });

}).call(this);


(function() {
  angular.module('wut').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $urlRouterProvider.otherwise('/');
    $stateProvider.state("home", {
      url: "/",
      templateUrl: "app/home/home.html",
      controller: 'HomeCtrl',
      resolve: {
        bootstrap: [
          '$http', function($http) {
            return $http.get('assets/data/bootstrap.json').then(function(res) {
              return res.data;
            });
          }
        ]
      }
    });
    return $stateProvider.state("about", {
      url: "/about",
      templateUrl: "views/about.html"
    });
  });

}).call(this);


(function() {
  angular.module('wut').constant('moment', moment);

}).call(this);


(function() {
  angular.module('wut').config(function($logProvider, toastrConfig) {
    'ngInject';
    $logProvider.debugEnabled(true);
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    return toastrConfig.progressBar = true;
  });

}).call(this);


angular.module("wut").run(["$templateCache", function($templateCache) {$templateCache.put("app/home/home.html","<div class=\"home\" ng-class=\"{\'home--right\': state == \'RIGHT\', \'home--wrong\': state == \'WRONG\'}\"><div class=\"home__background\" ng-if=\"bootstrap.background\" ng-style=\"backgroundStyle()\"></div><div class=\"home__question\"><h3>{{bootstrap.title}}</h3><p class=\"text-muted\" ng-bind-html=\"bootstrap.description\"></p><div><div class=\"home__question__label\" ng-bind-html=\"currentQuestion.label\"></div><div class=\"home__question__choices\"><fieldset class=\"row\" ng-disabled=\"state != \'PROMPT\'\"><div class=\"col-sm-6\" ng-repeat=\"choice in bootstrap.choices\"><button class=\"btn btn-lg btn-block btn-primary home__question__choices__single\" ng-class=\"choiceClass($index)\" ng-click=\"submit($index)\" ng-bind-html=\"choice.label || choice\"></button></div></fieldset></div></div></div></div>");}]);